:root{
  --back-white : white;
  --color-white: black;
  background: --back-white;
  color: --color-white;
}

html{
  position: relative;
  min-height: 100%;
  margin: 0;
}

* {
  font-family: 'Iropke Batang', serif; 
}

*:focus{
  outline: none;
}

.main {
  min-height: 100%;
  text-align: center;
  margin-top: 1em;
}

.nav{
  color: --color-white;
  margin-top: 30px;
  margin-left: 10px;
  margin-right: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid;
  border-color: --color-white;
  /* width: 100%; */
}

.nav a:visited{
  color: --color-white;
  text-decoration: none !important;
}

.nav a:hover{
  color: gray;
  transition: 0.2s;
}

.nav a{
  color: black;
  display: inline;
  margin-left: 10px;
  text-decoration: none !important;
}

a{
  color:black;
  text-decoration: none !important;
}

a:visited{
  color:black;
  text-decoration: none !important;
}

.login{
  float: right;
}

.userinfo{
  float: right;
  margin-right: 30px;
  margin-left: 10px;
}

.register{
  float: right;
  margin-right: 10px;
  margin-left: 0px;
}

p{
  margin:0px;
}

.JournalViewBox{
  margin-bottom: 30px;
  padding: 10px;
}

.JournalViewBoxDate{
  position: relative;
  left:0px;
  padding: 10px;
  margin: 10px;
  margin-right: 6em !important;
}

.JournalViewBoxDel:visited{
  color: black;
  text-decoration: none !important;
}

.JournalViewBoxDel:hover{
  color: gray;
  transition: 0.2s;
}

.JournalViewBoxDel{
  display: inline;
  margin-left: 10px;
  text-decoration: none !important;
  position: relative;
  right: 0px;
  padding: 10px;
  margin: 10px;
}

.JournalViewBoxFix:visited{
  color: black;
  text-decoration: none !important;
}

.JournalViewBoxFix:hover{
  color: gray;
  transition: 0.2s;
}

.JournalViewBoxFix{
  position: relative;
  display: inline;
  text-decoration: none !important;
}

.JournalViewBoxContent{
  padding-top: 50px;
  padding-bottom: 10px;
}

.JournalViewNav{
  margin: 10px;
}

.RegisterForm{ 
  margin-left: auto; 
  margin-right: auto; 
}

input{
  background-color: #cfd0d1;
  border: 0 !important;
  outline: 0 !important;
  box-shadow: 0 !important;
  width: 20em;
  height: 3em;
  margin: 5px;
  padding: 5px;
  transition: 0.3s;
}

input:hover{
  background: black;
  color:  #cfd0d1;
  transition: 0.3s;
}

input:hover::placeholder{
  color: #cfd0d1 !important;
  transition: 0.3s;
}

input:focus{
  background: black;
  color:  whitesmoke;
  transition: 0.3s;
}

input:focus::placeholder{
  color: #cfd0d1 !important;
  transition: 0.3s;
}

input::placeholder{
  opacity: 0.5;
  transition: 0.3s;
}


button {
  margin: 5px;
  color: #333;
  font-size: 15px;
  background: #cfd0d1;
  padding: 5px 10px 5px 10px;
  text-decoration: none;
  border: 0 !important;
  outline: 0 !important;
  box-shadow: 0 !important;
}

button:hover {
  transition: 0.3s;
  background: #333;
  color: white;
  text-decoration: none;
}

/* 저널 에디터의 크기 조절 */
.JournalEdit {
  font-size: 15px;
  width: 80%;
  min-height: 20em;
  max-height: 20em;
  margin-right: auto;
  margin-left: auto;
  border: none;
}

textarea::placeholder{
  opacity: 0.5;
}

.warp {
  min-height: 100% !important;
  position: relative !important;
  }


.Mainfooter{
  border-top: 1px solid;
  margin-top: 30px;
  margin-left: 10px;
  margin-right: 10px;
  padding-top: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
}

.GithubImg{
  width: 35px;
  height: auto;
  margin-left: 10px;
}

.mailImg{
    width: 35px;
  height: auto;
  margin-left: 10px;
}

/* 다크모드 적용 */
@media (prefers-color-scheme: dark) {
    :root{
      background: black;
      color: white;
    }

    input{
      background: #333;
      color: white;
      border:none;
      transition: 0.3s;
    }

    input:hover{
      background: #cfd0d1;
      color:  black;
      transition: 0.3s;
    }

    input:hover::placeholder{
      color: black !important;
      transition: 0.3s;
    }

    input:focus{
      background: #cfd0d1;
      color:  black;
      transition: 0.3s;
    }

    input:focus::placeholder{
      color: black !important;
      transition: 0.3s;

    }

    ::placeholder{
      color: whitesmoke !important;
      transition: 0.3s;
      
    }
    
    .nav{
      border-bottom: 1px solid;
      border-color: white;
    }

    .nav a:visited{
      color: white;
      text-decoration: none !important;
    }
    
    .nav a:hover{
      color: gray;
      transition: 0.2s;
      text-decoration: none !important;
    }
    
    .nav a{
      color: white;
      text-decoration: none !important;
    }

    button {
      color: #cfd0d1;
      background: #333;
      border: none;
      box-shadow: none;
    }

    button:hover{
      color: #333;
      background: #cfd0d1;
    }

    .GithubImg{
      filter: invert();
    }
    .mailImg{
         filter: invert();
    }

    .JournalViewBox{
      background-color: #333;
    }

    /* 저널 에디터 */
    .JournalEdit {
      color: white;
      background: #333;
    }

    .JournalEdit ::placeholder{
      color: #cfd0d1;
    }

    .JournalViewBoxDate ,.JournalViewBoxDel{
      color: white;
    }

    .JournalViewBoxFix{
      color: white;
      text-decoration: none !important;
    }
}


/* 팝업 모듈의 css */
.mm-popup {
  display: none;
  white-space: pre
}

.mm-popup--visible {
  display: block;
}

  .mm-popup__overlay {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      overflow: auto;
      background: rgba(0, 0, 0, .3);
  }

  .mm-popup__close {
      display: none;
  }

  .mm-popup__input {
      display: block;
      width: 100%;
      height: 30px;
      border-radius: 3px;
      background: #f5f5f5;
      border: 1px solid #e9ebec;
      outline: none;
      -moz-box-sizing: border-box !important;
      -webkit-box-sizing: border-box !important;
      box-sizing: border-box !important;
      font-size: 14px;
      padding: 0 12px;
      color: #808080;
  }

  .mm-popup__btn {
      border-radius: 3px;
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      padding: 0 10px;
      margin: 0;
      line-height: 32px;
      height: 32px;
      text-align: center;
      display: inline-block;
      font-size: 12px;
      font-weight: 400;
      color: #333;
      background: transparent;
      outline: none;
      text-decoration: none;
      cursor: pointer;
      z-index: 9999;
  }

  .mm-popup__btn--leftclose {
      width: 90%;
  }

  .mm-popup__btn--closeaccount{
    width: 40% !important;
  }

  .mm-popup__box {
      width: 350px;
      text-align: center;
      position: absolute;
      top: 35%;
      left: 50%;
      transform: translate(-50%, -50%);
      margin: 0 auto 0 auto;
      background: #fff;
      box-shadow: 0px 5px 20px 0px rgba(126,137,140,0.20);
      border: 1px solid black;
      overflow: hidden;
      z-index: 9999;
  }

      .mm-popup__box__header {
          padding: 15px 20px;
          background: #333;
          color: white;
          z-index: 9999;
      }

          .mm-popup__box__header__title {
              margin: 0;
              font-size: 16px;
              text-align: left;
              font-weight: 600;
          }

      .mm-popup__box__body {
          padding: 20px;
          line-height: 1.4;
          font-size: 14px;
          color: #454B4D;
          background: #fff;
          position: relative;
          z-index: 9999;
      }

          .mm-popup__box__body p {
              margin: 0 0 5px;
              
          }

      .mm-popup__box__footer {
          overflow: hidden;
          padding: 40px 20px 20px;
      }

          .mm-popup__box__footer__right-space {
              float: right;
          }

              .mm-popup__box__footer__right-space .mm-popup__btn {
                  margin-left: 5px;
              }

          .mm-popup__box__footer__left-space {
              float: left;
          }

              .mm-popup__box__footer__left-space .mm-popup__btn {
                  margin-right: 5px;
              }

  .mm-popup__box--popover {
      width: 300px;
      margin-left: -150px;
  }

      .mm-popup__box--popover .mm-popup__close {
          position: absolute;
          top: 5px;
          right: 5px;
          padding: 0;
          width: 20px;
          height: 20px;
          cursor: pointer;
          outline: none;
          text-align: center;
          border-radius: 10px;
          border: none;
          text-indent: -9999px;
          background: transparent url('data:image/png;base64,') no-repeat center center;
          background-size: 100%;
          margin: 0;
          z-index: 9999;
      }

      .mm-popup__box--popover .mm-popup__box__body {
          padding: 20px;
      }


